<template>
  <v-container>
    <v-card class="mt-5 transparent" flat>
      <v-card-title>
        <!-- <v-btn class="indigo mx-auto" dark tile large @click="mrq = true">
          </v-btn
        > -->
      </v-card-title>
      <v-card-text class="mt-4">
        <v-row justify="space-around">
          <v-col class="" cols="12" md="6" lg="3" @click="mrq = true">
            <div class="boxed plus_meeting pa-5">
              <v-avatar size="100" tile>
                <v-img src="../assets/add.png"></v-img
              ></v-avatar>
              <p class="my-2">Request a new</p>
              <h2>Advisory Meeting</h2>
            </div>
          </v-col>
          <v-col
            class="ma-2 boxed"
            style="height: 100%"
            cols="12"
            md="6"
            lg="3"
            v-for="(m, i) in meetings"
            :key="i"
          >
            <div class="pa-3 border_blue">
              <h3 class="text-center">Date &amp; Time</h3>
              <h2 class="text-center my-3 indigo--text">
                {{
                  m.datetime
                    ? new Date(m.datetime).toDateString() +
                      " " +
                      new Date(m.datetime).toLocaleTimeString()
                    : ""
                }}
              </h2>
              <h3 class="text-center my-3">
                Status: {{ m.status ? "Approved" : "Pending" }}
              </h3>
              <div class="px-1 py-3">
                <div class="row align-center" v-if="m.reason">
                  <v-avatar color="indigo" size="35" class="mr-1"
                    ><v-icon dark small> mdi-account </v-icon></v-avatar
                  >
                  <p
                    class="my-3 border_blue mr-5"
                    style="background: white; color: #000; max-width: 180px"
                  >
                    {{ m.reason }}
                  </p>
                </div>
                <div class="row justify-end align-center" v-if="m.reply">
                  <p
                    class="text-right ml-5 my-3 border_orange"
                    style="background: white; color: #000; max-width: 180px"
                  >
                    {{ m.reply }}
                  </p>
                  <v-avatar size="40" class="mr-1">
                    <v-img src="../assets/rlogo.svg"></v-img>
                  </v-avatar>
                </div>
              </div>

              <div class="text-center">
                <v-btn
                  :href="m.link"
                  color="indigo"
                  target="_blank"
                  dark
                  v-if="m.link"
                >
                  <v-icon>mdi-video-plus</v-icon>
                  {{
                    new Date(m.datetime).getTime() > Date.now()
                      ? "Meeting"
                      : "recorded"
                  }}
                  link
                </v-btn>
                <p class="text-center" v-else-if="m.status">
                  Meeting link will be available soon
                </p>
              </div>
            </div>
          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
    <v-dialog v-model="mrq" max-width="600">
      <v-card class="pb-4">
        <v-card-title>
          <h3
            class="mx-auto my-3 text-center indigo pa-2 white--text"
            style="border-radius: 5px"
          >
            Request an Advisory Meeting
          </h3>
        </v-card-title>
        <v-card-text>
          <div>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit.
              Quisquam, placeat ipsa iusto aut delectus sunt eos cumque amet
              repellendus temporibus? Mollitia officiis amet enim consequuntur
              quo velit iure dolor asperiores.
            </p>
          </div>
          <v-form ref="form" lazy-validation v-model="valid">
            <v-text-field
              label="Suggested Time (Not fixed)"
              type="datetime-local"
              v-model="meeting.date"
              outlined
              dense
            />
            <v-textarea
              outlined
              label="Reason for meeting"
              v-model="meeting.reason"
            ></v-textarea>
            <v-select
              v-model="meeting.duration"
              outlined
              dense
              :items="durations"
              @change="amountCalculate"
              label="Meeting Duration(minute)"
            />
            <v-text-field
              readonly
              v-model="meeting.amount"
              type="number"
              label="Meeting Fees"
              suffix=""
              outlined
              dense
            />
          </v-form>
        </v-card-text>
        <v-card-actions class="mt-n8">
          <v-btn
            class="mx-auto indigo"
            dark
            large
            tile
            :loading="loading"
            @click="meetingRequest"
            >Request &amp; Pay</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      meeting: {},
      valid: true,
      loading: false,
      meetings: [],
      mrq: false,
      mtype: "",
      durations: [
        { text: "30 minuntes", value: 30 },
        { text: "45 minutes", value: 45 },
        { text: "1 Hour", value: 60 },
        { text: "1 Hour 30 minutes", value: 90 },
        { text: "2 Hours", value: 120 },
      ],
    };
  },
  methods: {
    amountCalculate() {
      this.meeting.amount = this.meeting.duration * 10;
    },
    meetingRequest() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        axios
          .post("https://agile-ocean-73926.herokuapp.com/init", {
            type: "create",
            roll: this.user.displayName,
            ...this.meeting,
          })
          .then((res) => {
            window.open(res.data.url, "_blank");
            this.loading = false;
          })
          .catch((err) => {
            alert(err);
            this.loading = false;
          });
      }
    },
    getMeetings() {
      fetch(
        "https://script.google.com/macros/s/AKfycbxZwegkJojM8GImNcevhXJpbUBiwzCFFN2sn6TICkoOTEmPl6mV_OIlaIJlfdm-HL-RYA/exec?roll=" +
          this.user.displayName
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines.sort(
            (a, b) => b.meeting_id - a.meeting_id
          );
        });
    },
  },
  created() {
    this.getMeetings();
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
};
</script>

<style lang="scss" scoped>
.sections__main {
  display: grid;
  min-height: calc(100vh - 100px);
  place-items: center;
}
.plus_meeting {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  cursor: pointer;
  height: 100%;
  background: radial-gradient(
    circle,
    #7e84f5,
    #7683f3,
    #7082e4,
    #5073e5,
    #1164e5
  ) !important;

  color: #fff;

  transition: all 0.5s ease-in-out;

  &:hover {
    background: radial-gradient(
      circle,
      #f2d54b,
      #eac142,
      #e1ad3a,
      #d79a34,
      #cb872f
    ) !important;
    transform: scale(1.03);
    color: #000;
  }
}
.meetingtype {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-top: 10px;
  background: rgba(255, 255, 255, 0.89);
  border-radius: 10px;

  box-shadow: inset 2px 0 10px rgb(238, 238, 238);
  backdrop-filter: blur(10px) !important;
  transition: all 0.2s;

  &:hover {
    transform: scale(1.03);
  }
}
.boxed {
  border-radius: 5px;

  background: #f3f1f1;
  box-shadow: 2px 2px 6px #cfcdcd, -2px -2px 6px #ffffff;

  transition: all 0.5s ease-in-out;

  &:hover {
    color: #fff;
    background: radial-gradient(
      circle,
      #f2d54b,
      #eac142,
      #e1ad3a,
      #d79a34,
      #cb872f
    ) !important;
    transform: scale(1.03);
    color: #000;
  }
}
</style>